<template>
	<div class="recommend">
		<h2 class="recommendlist">推荐歌单</h2>
		<ul>
			<li v-for="item in songList">
				<a href="">
				<img :src="item.songImgSrc" />
				<p class="songName">{{item.songName}}</p>
				</a>
			</li>
			<li class="clearleft"></li>
		</ul>
		<h2 class="recommendlist">推荐歌单</h2>
		<m-songlist></m-songlist>
	</div>
</template>
<script type="text/ecmascript-6">
	import MSonglist from '../components/m-songlist'
	export default{
		data(){
			return {
				songList:[]
			}
		},
		methods: {
			getSongList() {
				var selfvue = this
				this.$axios.get("../../static/data/songList.json").then(function(response){
					selfvue.songList = response.data
				})
			}
		},
		mounted() {
            console.log(this.$router.options.routes);
			this.getSongList()
		},
		components: {
			"m-songlist": MSonglist
		}
	}
</script>
<style scoped  lang="stylus" rel="stylesheet/stylus">
    @import "../common/css/reset"
    .recommend
    	background #fcfcfd
    	height 300px
    	min-height 100%
    	ul
    		height 300px
    	.clearleft
    		clear left
    	li
    		width 32%
    		float left
    		padding-left 1%
    		position relative
    		text-align center
    		img
    			width 100%
    			height 100%
    			border-radius 10px
            .songName
    		    position absolute
    		    top 0px
    		    text-align center
    		    color white
    		    font-family "微软雅黑"	
    		    font-size 12px
    		    margin 0 auto
    .recommendlist
    	padding-left 9px
    	height 20px
    	line-height 20px
    	font-size 15px
    	margin-top 10px
    	text-align center
    	margin-bottom 10px
    	font-weight 400 
    	border-left 2px solid #d33a31
    	
</style>


